.carton-lleno-banner-back {
  background: red;  /* fallback for old browsers */
background: -webkit-linear-gradient(to top, black, red);  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to top, black, red); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

  animation: fadeIn 1s;
  opacity: 0.95;
  z-index: 0;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  position: fixed;
}

.carton-lleno-banner-content {
  /* background-color: black; */
  animation: enterScreen 1s;
  z-index: 10;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  position: fixed;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  color: white;
}
h1{
  text-shadow: black 0 2px;
}

.carton-lleno-banner-content .social-share-bar {
  display: flex;
  justify-content: center;
}
.carton-lleno-banner-content input{
  font-family: 'Poppins';
  text-align: center;
  background-color: white;
  color: red;
  padding: 1%;
  border: none;
  height: 2em;
}

@keyframes enterScreen {
  0% {
    transform: translateY(300px);
  }
  100% {
    transform: translateY(0);
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: .7;
  }
}