@import url(https://fonts.googleapis.com/css?family=Poppins:400,900&display=swap);
/**************** ESTILOS BASE *********************/
html{
  cursor: pointer;
}
::-moz-selection {
  background-color: black;
  color: white;
}
::selection {
  background-color: black;
  color: white;
}
::moz-selection {
  background-color: black;
  color: white;
}
.index {
  background-color: #e6e6e6;
}
.toggle {
  background-color: white;
  border: 1px solid black;
  padding: 1%;
}
.toggle:hover {
  background-color: black;
  color: white;
  border: 1px solid white;
}
#instrucciones {
  padding: 5%;
  margin-top: 2%;
  background-color: black;
  color: white;
  width: 40%;
  z-index: 99;
}
#instrucciones li {
  list-style: decimal;
}
.wrapper {
  margin: 1.5%;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 10px;
}
header {
  text-align: center;
}

.logo {
  max-width: 55%;
  margin-top: 30%;
}
main {
  font-family: "HelveticaNeue-Light", "Helvetica Neue Light", "Helvetica Neue",
    Helvetica, Arial, "Lucida Grande", sans-serif;
}

/******** Tabla ********************/
table {
  border-collapse: collapse;
  margin: auto;
  margin-top: 5%;
  margin-bottom: 5%;
  width: 100%;
}
table,
th,
td {
  border: 2px solid black;
  background-color:white ;
}
th,
td {
  padding: 10px;
  text-align: center;
  max-width: 130px;
}
th:hover,
td:hover {
  background-color: #d30707;
  color: black;
  font-family: "Times New Roman", Times, serif;
}
.aclaracion {
  font-size: 0.6em;
}
.siete {
  background-color: red;
  background-image: url(/criptobingo/static/media/siete.ad5419e6.png);
  background-size: 100%;
  background-repeat: no-repeat;
}
.siete:hover {
  background-color: red;
}
/******** Footer ********************/
body,
footer {
  font-family: "Poppins", sans-serif;
}
footer {
  text-align: right;
  margin-right: 2%;
  margin-bottom: 2%;
}
.boldfooter {
  font-weight: bold;
  font-size: 1.4em;
  margin-bottom: -1%;
}

/************** MOVILES *********************/
@media (max-width: 600px) {
  .wrapper {
    display: block;
  }
  th,
  td {
    font-size: 2.6vw;
    padding: 1%;
  }
  .siete {
    background-position: 50%;
  }
  header {
    margin-top: -25vw;
  }
  #instrucciones {
    width: 90%;
  }
  .carton-lleno img {
    max-width: 8 0%;
  }
  .toggle{
    padding: 2%;
  }
}

.carton-lleno-banner-back {
  background: red;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
background: -webkit-gradient(linear, left bottom, left top, from(black), to(red));
background: linear-gradient(to top, black, red); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

  -webkit-animation: fadeIn 1s;

          animation: fadeIn 1s;
  opacity: 0.95;
  z-index: 0;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  position: fixed;
}

.carton-lleno-banner-content {
  /* background-color: black; */
  -webkit-animation: enterScreen 1s;
          animation: enterScreen 1s;
  z-index: 10;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  position: fixed;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  color: white;
}
h1{
  text-shadow: black 0 2px;
}

.carton-lleno-banner-content .social-share-bar {
  display: flex;
  justify-content: center;
}
.carton-lleno-banner-content input{
  font-family: 'Poppins';
  text-align: center;
  background-color: white;
  color: red;
  padding: 1%;
  border: none;
  height: 2em;
}

@-webkit-keyframes enterScreen {
  0% {
    -webkit-transform: translateY(300px);
            transform: translateY(300px);
  }
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

@keyframes enterScreen {
  0% {
    -webkit-transform: translateY(300px);
            transform: translateY(300px);
  }
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

@-webkit-keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: .7;
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: .7;
  }
}
